import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
	data() {
		return {
			spec: '',
			childrenTicketPrice: '',
			adultTicketPrice: '',
			buylist: [], //订单列表
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			note: '' //备注
		};
	},
	onShow() {
		//页面显示时，加载订单信息
		uni.getStorage({
			key: 'buylist',
			success: ret => {
				console.log(ret);
				this.buylist = ret.data;
				this.goodsPrice = 0;
				//合计
				let len = this.buylist.length;

				for (let i = 0; i < len; i++) {
					if (this.buylist[i].spec == '规格:学生') {
						this.goodsPrice = this.goodsPrice + this.buylist[i].number * this.buylist[i].childrenTicketPrice;
					} else {
						this.goodsPrice = this.goodsPrice + this.buylist[i].number * this.buylist[i].adultTicketPrice;
					}
					// this.goodsPrice = this.goodsPrice + (this.buylist[i].number*this.buylist[i].price);
				}
				this.sumPrice = this.goodsPrice;
			}
		});
	},

	onBackPress() {
		//页面后退时候，清除订单信息
		this.clearOrder();
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		clearOrder() {
			uni.removeStorage({
				key: 'buylist',
				success: res => {
					this.buylist = [];
					console.log('remove buylist success');
				}
			});
		},
		toPay() {
			//商品列表
			let paymentOrder = [];
			let goodsid = [];
			let len = this.buylist.length;
			for (let i = 0; i < len; i++) {
				paymentOrder.push(this.buylist[i]);
				goodsid.push(this.buylist[i].scenicSpotId);
			}
			if (paymentOrder.length == 0) {
				uni.showToast({ title: '订单信息有误，请重新购买', icon: 'none' });
				return;
			}
			console.log(paymentOrder);
			//获取支付订单，把待支付订单list传给后端，返回一个订单id，在订单支付成功时将订单id传给后端。
			let arradd = [];
			console.log(this.buylist);
			let ordelist = this.buylist;
			console.log(ordelist.length);
			let that = this;
			console.log(this);
			let dataArr = function() {
				//生成订单list
				let i = 0;
				while (i < ordelist.length) {
					let objadd = {};
					objadd.number = ordelist[i].number;
					objadd.spec = ordelist[i].spec;
					objadd.ticketId = ordelist[i].scenicSpotId;
					objadd.userId = that.userId;
					arradd.push(objadd);
					i++;
				}
				return arradd;
			};
			let requestList = dataArr();

			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/add/payment/order',
				method: 'POST',
				data: {
					requestList
				},
				success: res => {
					console.log(res.data.data.ids);
					uni.hideLoading();
					setTimeout(() => {
						uni.setStorage({
							key: 'order_ids',
							data: res.data.data.ids
						});
					}, 200);
					uni.redirectTo({
						url: '../pay/payment/payment?amount=' + this.sumPrice
					});
				},
				fail: () => {},
				complete: () => {
				}
			});

		}
	}
};
